const Roster = () => (
  <div>
    <h2>!</h2>
  </div>
)
const Home = () => (
  <div>
    <h1>!</h1>
  </div>
)

const Header = () => (
  <header>
    <nav>
      <ul>
        <li><Link to='/'>Home</Link></li>
        <li><Link to='/roster'>Roster</Link></li>
        <li><Link to='/schedule'>Schedule</Link></li>
      </ul>
    </nav>
  </header>
)
const Schedule = () => (
  <div>
    <ul>
      <li></li>
      <li></li>
      <li>  </li>
    </ul>
  </div>
)


const Main = () => (
  <main>
    <Switch>
      <Route exact path='/' component={Home}/>
      <Route path='/roster' component={Roster}/>
      <Route path='/schedule' component={Schedule}/>
    </Switch>
  </main>
)


const App = () => (
  <div>
    <Header />
    <Main />
  </div>
)














ReactDOM.render((
  <HashRouter>
    <App />
  </HashRouter>
), document.body)
registerServiceWorker();



